.right-side {
    margin-right: 9%;
    width: 16%;
    border-left: 1px solid #D8D8D8;
    height: 100vh;
    box-sizing: border-box;
    position: sticky;
    display: flex;
    flex-direction: column;
    right: 0;
    padding: 20px 0 20px 28px;
    // overflow-y: scroll;
    // overflow-x: hidden;

    &::-webkit-scrollbar {
        width: 5px;
    }

    /* Track */
    &::-webkit-scrollbar-track {
        background: #f1f1f1;
    }

    /* Handle */
    &::-webkit-scrollbar-thumb {
        background: #888;
    }

    /* Handle on hover */
    &::-webkit-scrollbar-thumb:hover {
        background: #555;
    }

    &__input {
        border: none;
        width: 100%;
        background-color: transparent;
        padding-left: 35px;
        outline: none;
    }

    &-top {
        border-radius: 31px;
        box-sizing: border-box;
        background: #EFF3F4;
        position: relative;
        width: 100%;
        width: 173px;
        height: 40px;
        display: flex;
        align-items: center;
        margin-bottom: 20px;

        &::before {
            content: '';
            width: 21px;
            height: 21px;
            position: absolute;
            left: 10px;
            top: 0;
            bottom: 0;
            margin: auto;
            background-repeat: no-repeat;
            background-size: contain;
            background-image: url(../../assets/images/icon/search.svg);
        }
    }

    .trends {
        width: 100%;
        background: #F7F9F9;
        border-radius: 10px;
        padding: 26px 16px 30px 8px;
        margin-bottom: 20px;

        &-link {
            font-style: normal;
            font-weight: normal;
            font-size: 16px;
            line-height: 100%;
            color: #1DA1F2;
        }

        &__title {
            font-style: normal;
            font-weight: bold;
            font-size: 18px;
            line-height: 32px;
            margin-bottom: 20px;
            color: #000000;
        }

        &-top {
            margin-bottom: 25px;
            display: flex;
            width: 100%;
            justify-content: space-between;
            align-items: center;
        }

        &-item {
            &:not(:last-child) {
                margin-bottom: 15px;
            }
            svg {
                border-radius: 50%;
                padding: 5px;
                transition: all ease 0.5s;
                cursor: pointer;

                &:hover {
                    background-color: #f1f1f1;
                }
            }

            &__title {
                font-style: normal;
                font-weight: normal;
                font-size: 14px;
                line-height: 21px;
                color: rgba(57, 59, 65, 0.6);
            }

            &__subtitle {
                font-style: normal;
                font-weight: 600;
                margin-top: -32px;
                font-size: 16px;
                line-height: 27px;
                color: #000000;
            }
        }
    }

    .author {
        position: absolute;
        bottom: 20px;
        font-size: 14px;
        color: #1DA1F2;
    }
}