.sidebar-item {
    display: inline-block;
    margin-bottom: 20px;

    &.active {
        .sidebar-item-wrapper__title {
            font-weight: bold;
        }
    }

    &-wrapper {
        display: flex;
        align-items: center;

        svg {
            color: #000;
            margin-right: 22px;
        }

        img {
            margin-right: 22px;
        }

        &__title {
            font-style: normal;
            font-weight: normal;
            font-size: 18px;
            line-height: 24px;
            color: #000000;
        }
    }
}