.login {
    height: 100vh;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;

    &__img {
        width: 50px;
        height: 41px;
        margin-bottom: 36px;
    }

    &__title {
        font-style: normal;
        font-weight: 900;
        font-size: 42px;
        line-height: 49px;
        color: #000000;
        margin-bottom: 36px;
    }

    &__btn {
        background: #fff;
        border-radius: 76px;
        display: block;
        border: none;
        width: 100%;
        padding: 18px 0;
        cursor: pointer;
        font-style: normal;
        font-weight: bold;
        font-size: 18px;
        line-height: 24px;
        color: #000;
        position: relative;
        border: 1px solid #1DA1F2;
        margin: 0 auto;
        transition: all ease 0.4s;
        
        &:hover {
            box-shadow: 0 0 10px 1px #1DA1F2;
        }

        &:before {
            content: '';
            width: 30px;
            height: 30px;
            position: absolute;
            top: 0;
            bottom: 0;
            margin: auto;
            left: 5%;
            background-repeat: no-repeat;
            background-size: contain;
            background-image: url(../../assets/images/icon/google.png);
        }
    }
}