.message {
    padding: 10px 25px 25px 25px;
    width: 100%;
    display: flex;
    border-bottom: 1px solid #E6E6E6;

    &-left {
        margin-right: 15px;

        &__img {
            width: 40px;
            height: 40px;
            border-radius: 50%;
            object-fit: cover;
        }
    }

    &-right {
        width: 89%;

        &__name {
            font-style: normal;
            font-weight: bold;
            font-size: 20px;
            line-height: 27px;
            color: #000000;

            span {
                font-style: normal;
                font-weight: normal;
                font-size: 16px;
                line-height: 24px;
                margin-left: 5px;
            }
        }

        &__text {
            font-style: normal;
            font-weight: normal;
            font-size: 16px;
            line-height: 24px;
            color: #000000;
            margin-bottom: 20px;
        }

        &-wrapper {
            display: flex;
            justify-content: space-between;
            align-items: center;
            margin-bottom: 5px;

            svg {
                cursor: pointer;
                padding: 5px;
                transition: all ease 0.3s;

                &:hover {
                    border-radius: 50%;
                    background-color: #f1f1f1;
                }
            }
        }

        &__img {
            margin-bottom: 20px;
            border-radius: 20px;
        }

        &-bottom {
            display: flex;

        }
    }
}