.profile {
    width: 53%;
    height: 100vh;
    overflow-y: scroll;
    box-sizing: border-box;
    overflow-x: hidden;

    &::-webkit-scrollbar {
        width: 5px;
    }

    /* Track */
    &::-webkit-scrollbar-track {
        background: #f1f1f1;
    }

    /* Handle */
    &::-webkit-scrollbar-thumb {
        background: #888;
    }

    /* Handle on hover */
    &::-webkit-scrollbar-thumb:hover {
        background: #555;
    }

    &-top {
        padding: 22px 0 15px 30px;
        width: 100%;
        display: flex;
        align-items: center;
        border-bottom: 1px solid #C4C4C4;

        svg {
            margin-right: 40px;
            color: #000;
        }
    }

    &-bgi {
        width: 100%;
        height: 280px;
        background-repeat: no-repeat;
        background-size: cover;
        background-image: url(../../assets/images/img/user-bgi.png);
    }

    &-info {
        padding: 0 40px 0 20px;
        margin-bottom: 40px;


        &__name {
            font-style: normal;
            font-weight: bold;
            font-size: 24px;
            line-height: 32px;
            color: #000000;
        }

        &__email {
            font-style: normal;
            font-weight: normal;
            font-size: 14px;
            line-height: 21px;
            color: rgba(0,0,0,0.6);
            margin-bottom: 15px;
        }

        &__job {
            font-style: normal;
            font-weight: normal;
            font-size: 16px;
            line-height: 24px;
            color: #000000;
            margin-bottom: 15px;

            a {
                color: #1DA1F2;
            }
        }

        &-about {
            display: flex;
            align-items: center;
            margin-bottom: 30px;
            &-item {
                display: flex;
                align-items: center;
                &:not(:last-child) {
                    margin-right: 30px;
                }

                img {
                    margin-right: 8px;
                }

                &__title {
                    font-style: normal;
                    font-weight: normal;
                    font-size: 16px;
                    line-height: 24px;
                    color: rgba(0, 0, 0, 0.6);

                    &.telegram-link {
                        color: #1DA1F2;
                    }
                }
            }
        }

        &-follows {
            display: flex;
            &-item {
                display: flex;

                &:not(:last-child) {
                    margin-right: 30px;
                }
                &__num {
                    font-style: normal;
                    font-weight: normal;
                    font-size: 16px;
                    line-height: 24px;
                    color: #000000;
                    margin-right: 5px;
                }

                &__title {
                    font-style: normal;
                    font-weight: bold;
                    font-size: 16px;
                    line-height: 24px;
                    color: #000000;
                }
            }
        }

        &-top {
            display: flex;
            justify-content: space-between;
            align-items: flex-end;
            margin-top: -70px;
            margin-bottom: 10px;

            &__img {
                width: 150px;
                height: 150px;
                border-radius: 50%;
                object-fit: cover;
            }

            &__btn {
                border: 1px solid rgba(0, 0, 0, 0.4);
                box-sizing: border-box;
                border-radius: 50px;
                font-style: normal;
                font-weight: 600;
                font-size: 14px;
                line-height: 24px;
                color: #000000;
                padding: 10px 15px;
                cursor: pointer;
                background-color: transparent;
            }
        }
    }

    .profile-links-wrapper {
        border-bottom: 1px solid #C4C4C4;

        &-link {
            cursor: pointer;
            &:first-child {
                border-bottom: 2px solid #1DA1F2;
                // border-radius: 4px;
            }
        }

        div {
            padding: 0 0 15px 0;
            display: inline-block;
        }
    }

    &-tweets {
        padding: 20px 0;

        &__title {
            padding-left: 101px;
            font-style: normal;
            font-weight: 600;
            font-size: 15px;
            line-height: 21px;
            color: #536471;
            position: relative;
            margin-bottom: 15px;

            &::before {
                content: '';
                position: absolute;
                top: 0;
                bottom: 0;
                left: 70px;
                margin: auto;
                width: 20px;
                height: 20px;
                background-repeat: no-repeat;
                background-size: contain;
                background-image: url(../../assets/images/icon/pin.svg);
            }
        }
    }
}