.sidebar {
    padding-top: 21px;
    width: 16%;
    border-right: 1px solid #D8D8D8;
    height: 100vh;
    box-sizing: border-box;
    margin-left: 8%;
    position: sticky;
    left: 0;
    top: 0;
    bottom: 0;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    padding-bottom: 10px;

    &-top {
        &__logo {
            margin-bottom: 30px;
        }

        &-wrapper {
            display: flex;
            flex-direction: column;
        }
    }

    .tweet {
        background: #1D9BF0;
        border-radius: 27.5px;
        width: 90%;
        margin-right: auto;
        border: none;
        padding: 8px 0;
        font-style: normal;
        font-weight: bold;
        font-size: 16px;
        line-height: 24px;
        color: #FFFFFF;
        cursor: pointer;
    }

    &-bottom {
        display: flex;
        align-items: center;

        &-left {
            margin-right: 10px;
        }

        &-right {
            &__title {
                font-style: normal;
                font-weight: 600;
                font-size: 14px;
                line-height: 21px;
                color: #000000;
            }

            &__subtitle {
                font-style: normal;
                font-weight: normal;
                font-size: 12px;
                line-height: 21px;
                color: rgba(0,0,0,0.6);
                min-width: 80px;
            }
        }
    }
}