.message-item {
    display: flex;
    align-items: center;
    cursor: pointer;

    &:not(:first-child) {
        margin-left: 100px;
    }

    &__img {
        width: 24px;
        height: 24px;
        margin-right: 7px;
    }

    &__num {
        font-style: normal;
        font-weight: normal;
        font-size: 16px;
        line-height: 21px;
        color: #536471;
    }
}