.user {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 25px;

    &-left {
        display: flex;
        align-items: center;

        &__img {
            margin-right: 5px;
        }

        &-wrapper {
            &__name {
                font-style: normal;
                font-weight: 600;
                font-size: 16px;
                line-height: 24px;
                color: #000000;
            }
            &__email {
                font-style: normal;
                font-weight: normal;
                font-size: 14px;
                color: rgba(0,0,0.6);
            }
        }
    }

    &-right {
        background: #000000;
        border-radius: 50px;
        padding: 4px 8px;
        font-style: normal;
        font-weight: bold;
        font-size: 14px;
        line-height: 100%;
        color: #FFFFFF;
    }
}