.home {
    width: 53%;
    height: 100vh;
    overflow-y: scroll;
    box-sizing: border-box;
    overflow-x: hidden;

    &::-webkit-scrollbar {
        width: 5px;
    }

    /* Track */
    &::-webkit-scrollbar-track {
        background: #f1f1f1;
    }

    /* Handle */
    &::-webkit-scrollbar-thumb {
        background: #888;
    }

    /* Handle on hover */
    &::-webkit-scrollbar-thumb:hover {
        background: #555;
    }

    &-top {
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: 20px;
        box-sizing: border-box;
        border-bottom: 1px solid #D8D8D8;

        &__title {
            font-style: normal;
            font-weight: bold;
            font-size: 24px;
            line-height: 32px;
            color: #000000;
        }
    }

    .loader-wrapper {
        display: flex;
        justify-content: center;
        margin-top: 50px;
        margin-bottom: 50px;
    }

    .tweet-blog {
        padding: 15px 20px;
        box-sizing: border-box;
        display: flex;
        width: 100%;
        border-bottom: 1px solid #D8D8D8;

        &-left {
            margin-right: 15px;
        }

        &-right {
            width: 100%;

            &-form {
                width: 100%;

                &__input {
                    border: none;
                    width: 100%;
                    padding: 8px 20px;
                    box-sizing: border-box;
                    padding-left: 5px;
                    outline-color: #1D9BF0;
                    font-style: normal;
                    font-weight: 400;
                    font-size: 18px;
                    // line-height: 29px;
                    color: #828282;

                    &::placeholder {
                        font-style: normal;
                        font-weight: 400;
                        font-size: 18px;
                        // line-height: 29px;
                        color: #828282;
                    }
                }

                &-bottom {
                    display: flex;
                    justify-content: space-between;
                    align-items: center;
                    margin-top: 40px;

                    &-left {
                        &__img {
                            cursor: pointer;

                            &:not(:last-child) {
                                margin-right: 24px;
                            }
                        }
                    }

                    &-right {
                        &__btn {
                            background: #1DA1F2;
                            border: none;
                            padding: 15px 30px;
                            box-shadow: 0px 10px 20px rgba(0, 0, 0, 0.08);
                            border-radius: 120px;
                            font-style: normal;
                            font-weight: bold;
                            font-size: 17px;
                            line-height: 100%;
                            color: #FFFFFF;
                            cursor: pointer;

                            &:disabled {
                                opacity: 0.4;
                            }
                        }
                    }
                }
            }
        }
    }
}